body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    border-radius: 50%;
    border: 1px solid darkblue;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 64px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    margin: 8px;
    text-align: center;
    width: 64px;
  }

  .square.white {
    background-color: white;
  }

  .square.red {
    background-color: red;
  }

  .square.yellow {
    background-color: yellow;
  }
  
  li > button {
    font-family: "Verdana" sans-serif;
    font-size: 1em;
    text-align: left;
    color: blue;
    background: none;
    margin: 0;
    padding: 0;
    border: none;
    cursor: pointer;
  }

  li > button:focus {
    outline: none;
    box-shadow: none;
  }
  

  .game-board {
      border: 1px solid darkblue;
      background-color: blue;
      padding: 5px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  